import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Fade from '@material-ui/core/Fade';
import CircularProgress from '@material-ui/core/CircularProgress';

import SearchInput from './SearchInput';
import DataTable from './Table';
import GridList from './GridList';

import mockupData from './mockup_data';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  button: {
    margin: theme.spacing(2),
  },
  placeholder: {
    marginTop: 20,
    width: 'calc(96% + 8px)',
  },
  loading: {
    marginTop: 200,
  },
}));

const GraphPanel = () => {
  const classes = useStyles();
  const [query, setQuery] = React.useState({ status: 'main' });

  const handleClick = (event) => {
    setQuery({ status: 'progress' });
    debugger
    getData(event.target)
  };

  const getData = (topic, subtopic) => {
    let url;
    if (subtopic === undefined) {
      url = topic === undefined ? 'main.json' : `topic_${topic}.json`;
    } else {
      url = `data_${topic}_${subtopic}.json`;
    }
    fetch('data/'+url)
      .then(res => res.json())
      .then(
        (result) => {
          if (result.papers) setQuery({ status: 'success', data: result });
          else setQuery({ status: 'error', data: { message: '' } });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          setQuery({ status: 'error', data: error });
        }
      )
  }

  let content;
  switch (query.status) {
    case 'main':
      setQuery({ status: 'progress' });
      getData();
      break;
    case 'success':
      content = (
        <GridList tileData={query.data} onClick={handleClick} />
      );
      break;
    case 'progress':
      content = (
        <Fade
          in={query.status === 'progress'}
          style={{
            transitionDelay: query.status === 'progress' ? '500ms' : '0ms',
            textAlign: 'center', // TODO why here?
          }}
          unmountOnExit
        >
          <div>
            <CircularProgress />
            <div>loading...</div>
          </div>

        </Fade>
      );
      break;
    case 'error':
      console.debug('Error', query.data);
      content = (
        <div>Error: {query.data.message}</div>
      );
      break;
    default:

  }

  return content;
}

export default GraphPanel;
