import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
// import tileData from './tileData';

import CircularProgress from '@material-ui/core/CircularProgress';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: 500,
    height: 450,
  },
}));

/**
 * The example data is structured as follows:
 *
 * import image from 'path/to/image.jpg';
 * [etc...]
 *
 * const tileData = [
 *   {
 *     uid: 'uid',
 *     src: 'src',
 *     // img: image,
 *     title: 'Image',
 *     author: 'author',
 *     cols: 2,
 *   },
 *   {
 *     [etc...]
 *   },
 * ];
 */
export default function ImageGridList({ tileData, onClick }) {
  const classes = useStyles();
  const [query, setQuery] = React.useState({ status: 'progress' });

  return (query.status === 'progress') ? (
    <div className={classes.root}>
      <CircularProgress />
      <div>loading...</div>
    </div >

  ) : (
      <div className={classes.root}>
        <GridList cellHeight={160} className={classes.gridList} cols={3}>
          {tileData.map((tile) => (
            <GridListTile key={tile.uid} cols={tile.cols || 1} onClick={onClick}>
              <img src={tile.img || tile.src || 'topic.png'} alt={tile.title} />
            </GridListTile>
          ))}
        </GridList>
      </div >
    );
}
