import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';

import Box from '@material-ui/core/Box';
import Fade from '@material-ui/core/Fade';
import CircularProgress from '@material-ui/core/CircularProgress';

import SearchInput from './SearchInput';
import DataTable from './Table';
import Graph from './Graph';
import GraphPanel from './GraphPanel';

import mockupData from './mockup_data';

// const drawerWidth = 120;
const drawerWidth = '100%';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  // appBarShift: {
  //   marginLeft: drawerWidth,
  //   width: `calc(100% - ${drawerWidth}px)`,
  //   transition: theme.transitions.create(['width', 'margin'], {
  //     easing: theme.transitions.easing.sharp,
  //     duration: theme.transitions.duration.enteringScreen,
  //   }),
  // },
  menuButton: {
    marginRight: 36,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    // width: theme.spacing(7) + 1,
    // [theme.breakpoints.up('sm')]: {
    //   width: theme.spacing(9) + 1,
    // },
    width: theme.spacing(50) + 1,
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

export default function MiniDrawer() {
  const classes = useStyles();
  const theme = useTheme();
  const [input, setInput] = React.useState(''); // default input
  const [show, setShow] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const [query, setQuery] = React.useState({ status: 'idle' });
  const timerRef = React.useRef();

  const handleDrawerToggle = () => {
    setOpen(!open);
  };

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handleGraphToggle = () => {
    setShow(!show);
  };

  const handleChange = (event) => {
    setInput(event.target.value);
  };

  const handleSearch = (event) => {
    if (process.env.REACT_APP_MOCKUP_DATA === 'true') return handleSearchMockup(event);

    setQuery({ status: 'progress' });

    const url = new URL(process.env.REACT_APP_BACKEND || 'http://localhost:8000/papers');
    url.search = new URLSearchParams({ 'q': input });

    fetch(url,
      {
        headers: new Headers({
          'Authorization': 'ZHANG',
          'Account-ID': 'bin'
        })
      }
    )
      .then(res => res.json())
      .then(
        (result) => {
          if (result.papers) setQuery({ status: 'success', data: result });
          else setQuery({ status: 'error', data: { message: '' } });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          setQuery({ status: 'error', data: error });
        }
      )
  };

  const handleSearchMockup = (event) => {
    clearTimeout(timerRef.current);

    if (query.status !== 'idle') {
      setQuery({ status: 'idle' });
      return;
    }

    setQuery({ status: 'progress' });

    timerRef.current = setTimeout(() => {
      setQuery({ status: 'success', data: mockupData });
    }, 2000);
  };

  const columns = [
    {
      title: 'Title',
      field: 'title',
      // customFilterAndSearch: (term, rowData) => term == rowData.name.length,
      cellStyle: {
        fontWeight: 'bold',
      },
    },
    { title: 'Score', field: 'score', type: 'numeric' },
    { title: 'Date', field: 'date', type: 'date' },
    {
      title: 'Language',
      field: 'language',
      lookup: { en: 'English', de: 'German', fr: 'French' },
    },
    // { title: 'Year', field: 'year', type: 'numeric' },
    // {
    //   title: 'Country',
    //   field: 'country',
    //   lookup: { 1: 'Switzerland', 2: 'China', 3: 'Italy', 4: 'USA' },
    // },
  ];

  let content;
  switch (query.status) {
    case 'success':
      content = (
        <DataTable data={query.data.papers} columns={columns} />
      );
      break;
    case 'progress':
      content = (
        <Fade
          in={query.status === 'progress'}
          style={{
            transitionDelay: query.status === 'progress' ? '500ms' : '0ms',
            textAlign: 'center', // TODO why here?
          }}
          unmountOnExit
        >
          <div>
            <CircularProgress />
            <div>loading...</div>
          </div>

        </Fade>
      );
      break;
    case 'error':
      console.debug('Error', query.data);
      content = (
        <div>Error: {query.data.message}</div>
      );
      break;
    default:

  }


  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          // [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: true,
            })}
          >
            <MenuIcon />
          </IconButton>
          {/* <Box> */}
          <Typography variant="h6" noWrap style={{ marginRight: 200 }}>COVID-19 Papers</Typography>
          <SearchInput value={input} onChange={handleChange} onSearch={handleSearch} onMenu={handleDrawerToggle} onGraph={handleGraphToggle} />
          {/* </Box> */}
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
          [classes.hide]: !show,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <div className={classes.toolbar}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
          </IconButton>
        </div>
        <Divider />
        {content}

        {/* <List>
          {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
        <Divider />
        <List>
          {['All mail', 'Trash', 'Spam'].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List> */}

      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <GraphPanel />
        {/* <Graph /> */}
        <Typography variant="overline" color="textSecondary">
          COVID-19 Papers
        </Typography>
      </main>
    </div>
  );
}
